// src/routes/events.ts
import { Hono } from "hono";
import { z } from "zod";
import { zValidator } from "@hono/zod-validator";
import type { Env } from "../types";
import postgres from "postgres";
import { QuerySchema } from "../schemas/validation";

const events = new Hono<{ Bindings: Env }>();

// Validation schema - keeps things clean and type-safe
const ParamsSchema = z.object({
  deviceUuid: z.string().uuid("Must be a valid UUID"),
});
events.get("/", zValidator("query", QuerySchema), async (c) => {
  const { page, limit, sortBy, order } = c.req.valid("query");
  const offset = (page - 1) * limit;
  console.log("sort", sortBy);
  try {
    const sql = postgres(c.env.HYPERDRIVE.connectionString);

    // Build the query string for debugging
    const query = sql`
      SELECT 
        e.id,
        e.time,
        e.type,
        e."deviceId",
        ST_AsGeoJSON(e.location) as location,
        e."verifiedBy",
        e."verifiedAt",
        e."createdAt",
        e."updatedAt",
        e."updatedBy",
        COALESCE(
            json_agg(
            json_build_object(
                'id', r.id,
                'w', r.w,
                'h', r.h,
                'x', r.x,
                'y', r.y
            )
            ) FILTER (WHERE r.id IS NOT NULL),
            '[]'
        ) as regions
        FROM "Events" e
        LEFT JOIN "Regions" r ON e.id = r."eventId"
        GROUP BY e.id
        ORDER BY ${sortBy + " " + order.toUpperCase()}
        LIMIT ${limit}
        OFFSET ${offset}
    `;

    // Log the actual query
    console.log("Executing query:", query.toString());

    const [countResult, events] = await Promise.all([
      sql`SELECT COUNT(*) FROM "Events"`,
      query,
    ]);

    await sql.end();

    const totalCount = parseInt(countResult[0].count);
    const totalPages = Math.ceil(totalCount / limit);

    return c.json({
      data: events,
      pagination: {
        currentPage: page,
        totalPages,
        totalCount,
        hasNextPage: page < totalPages,
        hasPrevPage: page > 1,
      },
    });
  } catch (error) {
    console.error("Failed to fetch events:", error);
    // Log the full error details
    console.error("Full error:", error);
    return c.json(
      {
        error: "Failed to fetch events",
        details: error instanceof Error ? error.message : "Unknown error",
      },
      500
    );
  }
});

events.get("/:deviceUuid", zValidator("param", ParamsSchema), async (c) => {
  const { deviceUuid } = c.req.valid("param");

  try {
    const sql = postgres(c.env.HYPERDRIVE.connectionString);

    const results = await sql`
      SELECT 
        e.id,
        e.time,
        e.type,
        e.location,
        e.verified_by as "verifiedBy",
        e.verified_at as "verifiedAt"
      FROM Events e
      JOIN Devices d ON d.id = e.device_id
      WHERE d.serial = ${deviceUuid}
      ORDER BY e.time DESC
      LIMIT 100
    `;

    await sql.end();
    return c.json(results);
  } catch (error) {
    console.error("Failed to fetch events:", error);
    return c.json(
      {
        error: "Failed to fetch events",
        details: error instanceof Error ? error.message : "Unknown error",
      },
      500
    );
  }
});

export default events;
